body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.landing_background { 
        background: url(/react_portfolio/static/media/coding2.053e654c.jpg) no-repeat center center fixed;
        background-size: cover;
        height: 100vh;
}

.img_overlay {
    background: rgba(0, 0, 0, .6);
    height: 100vh;
}

div.lp_text {
    color: white;
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 90vh;
}

@media screen and (max-width: 760px) {
    div.lp_text {
        padding: 0px 20px;
        font-size: 25px;
    }
}

.top_nav {
    font-size: 20px;
    margin: 40px;
    float: right;
}

.main_header {
    color: white;
}
.navbar {
   font-size: 20px;
   padding: 10px;
}

.navbar-brand {
    font-family: 'Kumar One Outline', cursive;
}

.nav-link {
    color: white !important;
}
.aboutme_wrapper {
    height: 100vh;
}

.about_heading {
    color: #3c3b7e;
    font-size: 50px;
    margin-top: 30px;
}
.App {
  font-family: 'Inconsolata', monospace;
}
